
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.modal-container {
  max-width: 90%;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  color: #333;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  position: relative;
  max-height: 95vh;
}
@media screen and (min-width: 800px) {
.modal-container {
    max-width: 60%;
}
}
@media screen and (min-width: 1300px) {
.modal-container {
    max-width: 50%;
}
}
@media screen and (min-width: 1800px) {
.modal-container {
    max-width: 40%;
}
}
.modal-header h3 {
  margin-top: 0;
}
.modal-close {
  position: absolute;
  top: 15px;
  right: 20px;
  display: inline-block;
  cursor: pointer;
}
.modal-default-button {
  float: right;
}
// /////////////
.modal-enter {
  opacity: 0;
}
.modal-leave-active {
  opacity: 0;
}
.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}



